.resume {
    margin-top: var(--mar-1);
    text-align: center;
}

.resume-bg {
    background-color: var(--first-color-second);
    padding-top: var(--mar-1-5);
    padding-bottom: 4.5rem;
}

.resume-title {
    margin-top: var(--mar-1);
}

.resume-subtitle {
    margin-bottom: var(--mar-1-5);
}

.resume-desc {
    display: none;
    margin: 0 auto;
}

.resume-title,
.resume-subtitle,
.resume-desc {
    color: #fff;
}

.resume-img {
    width: 232px;
    justify-self: center;
}

.resume-button {
    display: flex;
    justify-content: center;
}

/*==================== MEDIA QUERIES ====================*/
/* For medium devices */
@media screen and (min-width: 568px) {
    .resume-container {
        grid-template-columns: repeat(2, 1fr);
    }

    .resume-bg {
        padding-bottom: var(--mar-2);
    }

    .resume-subtitle {
        margin-bottom: var(--mar-0-75);
    }

    .resume-desc {
        display: block;
        margin-bottom: var(--mar-1-5);
        width: 250px;
    }

    .resume-img {
        padding-top: var(--mar-1-5);
    }
}

@media screen and (min-width: 768px) {
    .resume.section {
        padding-top: 9rem;
    }

    .resume {
        text-align: initial;
    }

    .resume-title {
        margin-top: 0;
    }

    .resume-desc {
        margin-left: 0;
    }

    .resume-button {
        justify-content: flex-start;
    }

    .resume-bg {
        background: none;
    }

    .resume-container {
        background-color: var(--first-color-second);
        border-radius: 1rem;
        padding: 3rem 2.5rem 0;
        grid-template-columns: 1fr max-content;
        column-gap: 3rem;
    }

    .resume-data {
        padding-top: .8rem;
    }

    .resume-img {
        width: 320px;
        padding: 0 0 3rem;
        margin-top: -1.5rem;
    }
}

/* For large devices */
@media screen and (min-width: 1024px) {
    .resume-desc {
        width: 260px;
    }
}