.contact-container {
    row-gap: 3rem;
}

.contact .section-subtitle {
    margin-bottom: var(--mar-2);
}

.contact-left {
    margin: 0 auto;
}

.contact-desc {
    max-width: 290px;
    margin-bottom: var(--mar-2-5);
}

.contact-info {
    display: flex;
    margin: 0 auto;
    margin-bottom: var(--mar-2);
}

.contact-icon {
    font-size: 2em;
    color: var(--first-color);
    margin-right: var(--mar-0-75);
}

.contact-title {
    font-size: var(--h3-font-size);
    font-weight: var(--font-semi-bold);
}

.contact-subtitle {
    font-size: var(--small-font-size);
    color: var(--text-color-light);
}

.contact-socials a {
    font-size: 1.75em;
    margin-top: var(--mar-0-25);
    margin-right: var(--mar-1-5);
}

.contact-inputs {
    gap: 0;
}

.contact-content {
    background-color: var(--input-color);
    border-radius: .5rem;
    padding: .75rem 1rem .25rem;
    margin-bottom: var(--mar-1);
}

.contact-content textarea {
    resize: none;
}

.contact-label {
    font-size: var(--smaller-font-size);
    color: var(--title-color);
}

.contact-input {
    width: 100%;
    background-color: var(--input-color);
    color: var(--text-color);
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
    border: none;
    outline: none;
    padding: .25rem .5rem .5rem 0;
}

/*==================== MEDIA QUERIES ====================*/
/* For medium devices */
@media screen and (min-width: 568px) {
    .contact-container {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (min-width: 768px) {
    .contact.section {
        padding-top: var(--mar-2);
    }
  
    .contact .section-subtitle {
        margin-bottom: 4rem;
    }
  
    .contact-desc {
        width: 250px;
        margin: 0 0 var(--mar-2-5);
    }
  
    .contact-inputs {
        grid-template-columns: repeat(2, 1fr);
        gap: var(--mar-0-75);
    }
}
  
/* For large devices */
@media screen and (min-width: 1024px) {
    .contact .section-subtitle {
        margin-bottom: var(--mar-2);
    }

    .contact-form {
        width: 460px;
    }
}