.header {
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: var(--body-color);
}

nav {
    max-width: 968px;
    height: var(--header-height);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo,
.nav-button {
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.logo:hover {
    color: var(--first-color);
}
  
.nav-button {
    font-size: 1.1em;
    cursor: pointer;
}

.nav-button:hover {
    color: var(--first-color);
}

@media screen and (max-width: 767.98px) {
    .nav-menu {
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--body-color);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, .15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: .3s;
    }
  
    .nav-list {
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }
  
    .nav-link {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: var(--small-font-size);
        color: var(--title-color);
        font-weight: var(--font-medium);
    }
  
    .nav-icon {
        font-size: 1.2em;
    }
  
    .nav-close {
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5em;
        cursor: pointer;
        color: var(--first-color);
    }
  
    .nav-close:hover {
        color: var(--first-color-alt);
    }
}

/* Show menu */
.show-menu {
    bottom: 0;
}

/* Active link */
.active-link {
    color: var(--first-color);
}

/* Change background header */
.scroll-header {
    box-shadow: 0 -1px 4px rgba(0, 0, 0, .15);
}

/*========== Theme Change Switch ==========*/
.nav-btns {
    display: flex;
    align-items: center;
}

.theme-button {
    font-size: 1.25em;
    color: var(--title-color);
    margin-right: var(--mar-1);
    cursor: pointer;
}

.theme-button:hover {
    color: var(--first-color);
}

/*==================== MEDIA QUERIES ====================*/
/* For small devices */
@media screen and (max-width: 350px) {
    .nav-menu {
        padding: 2rem .25rem 4rem;
    }
    
    .nav-list {
        column-gap: 0;
    }
}

/* For medium devices */
@media screen and (min-width: 768px) {
    .header {
        top: 0;
        bottom: initial;
        padding: 0 1rem;
    }

    .nav {
        height: calc(var(--header-height) + 1.5rem);
        column-gap: 1rem;
    }
    
    .nav-icon,
    .nav-close,
    .nav-button {
        display: none;
    }

    .nav-list {
        display: flex;
        column-gap: 2rem;
    }

    .nav-link {
        color: var(--title-color);
    }
    
    .nav-link:hover {
        color: var(--first-color-second);
    }

    .nav-link.active-link {
        color: var(--first-color);
        font-weight: var(--font-semi-bold);
    }

    .nav-menu {
        margin-left: auto;
    }

    .theme-button {
        margin: 0;
        padding-left: var(--mar-0-5);
    }
}

/* For large devices */
@media screen and (min-width: 1024px) {
    .header {
        padding: 0;
    }
}