.about .section-subtitle {
  margin-bottom: var(--mar-1-5);
}

.about-img {
  background: url(../../assets/img/about.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: inset 0 0 0 9px rgba(255, 255, 255, 0.3);
  width: 180px;
  height: 180px;
  justify-self: center;
  animation: about-animate 8s ease-in-out infinite 1s;
}

@keyframes about-animate {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }

  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }

  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}

.about-desc {
  text-align: center;
  margin-bottom: var(--mar-2);
}

.about-info {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: var(--mar-2-5);
}

/*==================== MEDIA QUERIES ====================*/
/* For medium devices */
@media screen and (min-width: 568px) {
  .about .section-subtitle {
    margin-bottom: var(--mar-2);
  }

  .about-img {
    width: 250px;
    height: 250px;
  }

  .about-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 768px) {
  .about .section-subtitle {
    margin-bottom: 4rem;
  }

  .about-container {
    column-gap: 5rem;
  }

  .about-desc {
    text-align: initial;
  }

  .about-info {
    justify-content: space-between;
  }

  .about-button {
    justify-content: initial;
  }
}