.home-container {
    gap: 1rem;
}

.home-content {
    grid-template-columns: .5fr 3fr;
    padding-top: 3.5rem;
    align-items: center;
}

.home-socials {
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}

.home-blob {
    width: 200px;
    fill: var(--first-color-second);
}

.blob-img {
    width: 170px;
}

.home-data {
    grid-column: 1/3;
}

.home-title {
    font-size: var(--big-font-size);
}

.home-subtitle {
    font-size: var(--h3-font-size);
    color: var(--first-color);
    font-weight: var(--font-medium);
    margin-bottom: var(--mar-0-75);
    width: fit-content;
    white-space: nowrap;
    border-right: 3px solid var(--text-color);
    padding-right: .5rem;
    overflow: hidden;
    animation: 
        typing 2s steps(20),
        cursor .6s step-end infinite alternate;
}

/* Blinking cursor */
@keyframes cursor {
    50% {
        border-color: transparent;
    }
}

/* Typing effect */
@keyframes typing {
    from {
        width: 0;
    }
    to {
        width: 60%;
    }
}

.home-desc {
    margin-bottom: var(--mar-2);
}

.home-scroll {
    display: none;
}

.home-scroll .scroll-arrow {
    color: var(--first-color);
    transition: 1s ease;
}

.home-scroll:hover .scroll-arrow {
    transform: translateY(.5rem);
    transition: 1s ease;
}

/*==================== MEDIA QUERIES ====================*/
/* For small devices */
@media screen and (max-width: 350px) {
    .home-content {
        grid-template-columns: .5fr 3fr;
    }

    .home-blob {
        width: 180px;
    }
}

/* For medium devices */
@media screen and (min-width: 568px) {
    .home-content {
        grid-template-columns: max-content 1fr 1fr;
    }
  
    .home-data {
        grid-column: initial;
    }
  
    .home-title {
        width: 280px;
    }
  
    .home-img {
        order: 1;
        justify-self: center;
    }
  
    .home-blob {
        padding-right: var(--mar-1-5);
    }
}

@media screen and (min-width: 768px) {
    .home-blob {
        padding-right: 0;
    }

    .home-container {
        row-gap: 5rem;
    }

    .home-content {
        padding-top: 5.5rem;
        column-gap: 2rem;
    }

    .home-blob {
        width: 270px;
    }

    .home-scroll {
        display: block;
        margin-left: 3rem;
    }
}

/* For large devices */
@media screen and (min-width: 1024px) {
    .home-title {
        font-size: var(--h1-font-size);
        width: 340px;
    }
  
    .home-blob {
        width: 320px;
    }
  
    .home-socials {
        transform: translateX(-6rem);
    }
}