/*==================== GOOGLE FONTS ====================*/
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

/*==================== VARIABLES CSS ====================*/
:root {
  --header-height: 3rem;

  /*========== Colors ==========*/
  /* Change favorite color */
  --primary-color: 31;
  --secondary-color: 220;

  /* HSL color mode */
  --first-color: hsl(var(--primary-color), 100%, 50%);
  --first-color-second: hsl(var(--primary-color), 80%, 50%);
  --first-color-alt: hsl(var(--primary-color), 100%, 63%);
  --first-color-lighter: hsl(var(--primary-color), 100%, 78%);
  --title-color: hsl(var(--primary-color), 8%, 30%);
  --text-color: hsl(var(--primary-color), 8%, 45%);
  --text-color-light: hsl(var(--primary-color), 8%, 65%);
  --input-color: hsl(var(--primary-color), 70%, 96%);
  --body-color: hsl(var(--primary-color), 60%, 99%);
  --container-color: #fff;
  --scroll-bar-color: hsl(var(--primary-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--primary-color), 12%, 80%);

  /*========== Font and typography ==========*/
  --body-font: 'Poppins', sans-serif;

  /* .5rem = 8px, 1rem = 16px, 1.5rem = 24px ... */
  --big-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;

  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== Margins ==========*/
  /* .25rem = 4px, .5rem = 8px, .75rem = 12px ... */
  --mar-0-25: .25rem;
  --mar-0-5: .5rem;
  --mar-0-75: .75rem;
  --mar-1: 1rem;
  --mar-1-5: 1.5rem;
  --mar-2: 2rem;
  --mar-2-5: 2.5rem;
  --mar-3: 3rem;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

/* Font size for large devices */
@media screen and (min-width: 1024px) {
  :root {
    --big-font-size: 3rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

/*========== Variables Dark theme ==========*/
body.dark-mode {
  /* HSL color mode */
  --title-color: hsl(var(--secondary-color), 8%, 95%);
  --text-color: hsl(var(--secondary-color), 8%, 85%);
  --input-color: hsl(var(--secondary-color), 2%, 35%);
  --body-color: hsl(var(--secondary-color), 0%, 20%);
  --container-color: hsl(var(--secondary-color), 2%, 35%);
  --scroll-bar-color: hsl(var(--secondary-color), 12%, 48%);
  --scroll-thumb-color: hsl(var(--secondary-color), 12%, 36%);
}

/*==================== BASE ====================*/
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0 0 var(--header-height) 0;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
  overflow-x: hidden;
  overflow-y: auto;
}

h1, h2, h3, h4 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

/*==================== REUSABLE CSS CLASSES ====================*/
.section {
  padding: 2rem 0 4rem;
}

.section-title {
  font-size: var(--h1-font-size);
  color: var(--title-color);
}

.section-subtitle {
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: var(--mar-3);
}

.section-title, 
.section-subtitle {
  text-align: center;
}

.socials-icon {
  font-size: 1.5em;
  color: var(--first-color);
}

.socials-icon:hover {
  color: var(--first-color-alt);
}

/* Buttons */
.button {
  display: inline-block;
  background-color: var(--first-color);
  color: #fff;
  padding: 1rem;
  border-radius: .5rem;
  font-weight: var(--font-medium);
}

.button:hover {
  background-color: var(--first-color-alt);
}

.button-icon {
  font-size: 1.25em;
  margin-left: var(--mar-0-5);
  transition: .3s;
}

.button-alt {
  background-color: var(--container-color);
  color: var(--title-color);
}

.button-alt:hover {
  background-color: var(--container-color);
  color: var(--text-color);
}

.button-flex {
  display: inline-flex;
  align-items: center;
}

.button-small {
  padding: .75rem 1rem;
}

.button-title {
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  margin-left: var(--mar-0-5);
  margin-right: var(--mar-0-5);
}

.button-link {
  padding: 0;
  background-color: transparent;
  color: var(--first-color);
  cursor: pointer;
}

.button-link:hover {
  background-color: transparent;
  color: var(--first-color-alt);
}

button[type="submit"] {
  background-color: var(--first-color);
  color: #fff;
  border: none;
  font-size: 1.1em;
  font-weight: var(--font-medium);
}

/*==================== LAYOUT ====================*/
.container {
  max-width: 768px;
  margin-left: var(--mar-1-5);
  margin-right: var(--mar-1-5);
}

.grid {
  display: grid;
  gap: 1.5rem;
}

/*========== SCROLL BAR ==========*/
::-webkit-scrollbar {
  width: .6rem;
  background-color: var(--scroll-bar-color);
  border-radius: .5rem;
}

::-webkit-scrollbar-thumb {
  background-color: var(--scroll-thumb-color);
  border-radius: .5rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--text-color-light);
}

/*==================== MEDIA QUERIES ====================*/
/* For small devices */
@media screen and (max-width: 350px) {
  .container {
    margin-left: var(--mar-1);
    margin-right: var(--mar-1);
  }
}

/* For medium devices */
@media screen and (min-width: 768px) {
  .container {
    margin-left: auto;
    margin-right: auto;
  }

  body {
    margin: 0;
  }

  .section {
    height: 84vh;
    padding: 4rem 0 2rem;
  }

  .section-title {
    padding-top: 4rem;
  }

  .section-subtitle {
    margin-bottom: 4rem;
  }

  .main {
    padding: 0 1rem;
  }
}

/* For large devices */
@media screen and (min-width: 1024px) {
  .main {
    padding: 0;
  }
}

@media screen and (min-width: 1200px) {
  .section {
    height: 85vh;
  }
}