.scroll-up {
    position: fixed;
    right: 1rem;
    bottom: -20%;
    background-color: var(--first-color);
    opacity: .8;
    padding: 0 .3rem;
    border-radius: .4rem;
    z-index: var(--z-tooltip);
    transition: .4s;
}

.scroll-up:hover {
    background-color: var(--first-color-second);
}

.scroll-icon {
    font-size: 1.5em;
    color: #fff;
    padding: .5rem .3rem;
}

/* Show scroll */
.show-scroll {
    bottom: 3.75rem;
}

/*==================== MEDIA QUERIES ====================*/
/* For large devices */
@media screen and (min-width: 1024px) {
    .scroll-up {
      right: 2.5rem;
      padding: 0.25rem .4rem;
    }
}