.project-container {
    padding: 2rem 15%;
}

.project .section-subtitle {
    margin-bottom: 0;
}

.project-content {
    padding: 0 1.5rem;
    margin: 0 auto !important;
}

.project-img {
    border-radius: .5rem;
    justify-self: center;
}

.title-container {
    display: flex;
    justify-content: space-between;
}

.project-title {
    font-size: var(--h3-font-size);
    margin-bottom: var(--mar-0-5);
}

.project-read {
    margin-bottom: var(--mar-0-5);
}

.project-read .read-arrow {
    transition: 1s ease;
}

.project-read:hover .read-arrow {
    transform: translateX(.5rem);
}

.project-desc {
    margin-bottom: var(--mar-0-75);
}

.project-button {
    margin-right: .18rem;
}

/* Swiper CSS */
.swiper-slide {
    opacity: 0;
    width: 265px;
}

.swiper-slide-active {
    opacity: 1;
    transition: .5s;
}

.swiper-button-prev::after,
.swiper-button-next::after {
    content: '';
}

.swiper-project-icon {
    font-size: 2em;
    color: var(--first-color);
}

.swiper-button-prev {
    left: 0;
}

.swiper-button-next {
    right: 0;
}

.swiper-button-prev,
.swiper-button-next {
    visibility: hidden;
}

.swiper-horizontal > .swiper-pagination-bullets {
    margin-top: 1rem;
}

.swiper-pagination-bullet-active {
    background: var(--first-color);
}

.swiper-button-prev,
.swiper-button-next,
.swiper-pagination-bullet {
    outline: none;
}

/* Project Modal */
.project-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .5);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    z-index: var(--z-modal);
    opacity: 0;
    visibility: hidden;
    transition: .3s;
}

.modal-content {
    position: relative;
    background-color: var(--container-color);
    padding: 1.5rem;
    border-radius: .5rem;
}

.modal-title {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mar-1-5);
}

.modal-close {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    font-size: 1.5em;
    color: var(--text-color);
    cursor: pointer;
}

.modal-icon {
    color: var(--first-color);
    margin-bottom: var(--mar-0-25);
    font-size: 2em;
}

.active-modal {
    opacity: 1;
    visibility: visible;
}

/*==================== MEDIA QUERIES ====================*/
/* For small devices */
@media screen and (max-width: 375.98px) {
    .project-content {
        padding: 0;
    }
}

/* For medium devices */
@media screen and (min-width: 568px) {
    .project-container {
        padding: 2rem;
    }
    
    .project-content {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .project-img {
        width: 215px;
    }

    .title-container {
        width: 240px;
    }
}

@media screen and (min-width: 768px) {
    .project-data {
        align-items: center;
    }

    .project-img {
        width: 320px;
    }
  
    .project-title {
        margin-bottom: .1rem;
    }
  
    .title-container {
        display: block;
    }
  
    .project-read .button-title {
        margin-left: 0;
    }
  
    .project-content {
        align-items: center;
    }
  
    .modal-content {
        width: 450px;
    }

    .project .section-title {
        margin-top: var(--mar-3);
    }
  
    .project-content {
        padding: 0 3.5rem;
    }
  
    .swiper-button-prev,
    .swiper-button-next {
        visibility: visible;
    }
}

/* For large devices */
@media screen and (min-width: 1024px) {
    .project-content {
        column-gap: 5rem;
    }
  
    .swiper-project-icon {
        font-size: 3.5em;
    }
}